import React, { Component } from 'react';
import {Grid, Row, Col, Jumbotron, Button, Image, Modal, Tooltip, OverlayTrigger, Popover} from 'react-bootstrap'
import './App.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import shrimporleans from './img/shrimporleans.jpg';
import ReactDOM from 'react-dom';
import orleans from './img/orleans.jpg';
import char from './img/char.jpg';
import rockefeller from './img/rockefeller.jpg';
import raw from './img/raw.jpg';
import bienville from './img/bienville.jpg';
import sampler from './img/sampler.jpg';



class Dessert extends Component {
  render(){
    return(
      <Grid>
      <div>
      
      <button data-toggle="modal" data-target="#exampleModal">
        Desserts
      </button>


<div class="modal fade" id="exampleModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
  <div class="modal-dialog" role="document">
    <div class="modal-content">

      <div class="modal-header">
         <h1 class="modal-title header-text center grey-shadow" id="exampleModalLabel">Oysters</h1>
        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
          <span aria-hidden="true">&times;</span>
        </button>
      </div>

      <div class="modal-body">

      </div>

      <Row>
        <Col sm={6}>
        <center>
          <Image id="menuimage" src={sampler}></Image>
          <h3 class="header-text crimson">Oyster Sampler 19.5</h3>
          <p class="script-text">The Best of the Best! Combination of three Charbroiled Oysters, 
            three Oysters Bienville, three Oysters Rockefeller and three Oysters Orleans.</p>
        </center>
        </Col>
        <Col sm={6}>
          <center>
          <Image id="menuimage" src={raw}></Image>
          <h3 class="header-text crimson">Oysters on the Half Shell</h3>
          <h5 class="header-text"> Half Dozen 9  Dozen 15.5</h5>
          <p class="script-text">Gulf oysters freshly shucked in-house.</p>
        </center>
        </Col>
      </Row>
      <Row>
        <Col sm={6}>
        <center>
          <Image id="menuimage" src={char}></Image>
          <h3 class="header-text crimson">Charbroiled Oysters</h3>
          <h5 class="header-text"> Half Dozen 10  Dozen 18</h5>
          <p class="script-text">Half Shell's signature oyster, charbroiled over an open flame with our unique white wine,
          butter, garlic and herb sauce</p>
        </center>
        </Col>
        <Col sm={6}>
        <center>
          <Image id="menuimage" src={orleans}></Image>
          <h3 class="header-text crimson">Oysters Orleans</h3>
          <h5 class="header-text"> Half Dozen 10  Dozen 18</h5>
          <p class="script-text">Half Shell's Award Winning oyster! Charbroiled over an open 
            flame and basted with our New Orleans style smokey Cajun sauce</p>
        </center>
        </Col>
      </Row>
      <Row>
        <Col sm={6}>
        <center>
          <Image id="menuimage" src={bienville}></Image>
          <h3 class="header-text crimson">Oysters Bienville</h3>
          <h5 class="header-text"> Half Dozen 11  Dozen 19.5</h5>
          <p class="script-text">Topped with an herb, mushroom, crab, Gulf shrimp, bacon and Italian bread crumb mixture, 
            then baked and finished with Parmesan cheese.</p>
        </center>
        </Col>
        <Col sm={6}>
        <center>
          <Image id="menuimage" src={rockefeller}></Image>
          <h3 class="header-text crimson">Oysters Rockefeller</h3>
          <h5 class="header-text"> Half Dozen 10  Dozen 18</h5>
          <p class="script-text">Topped with fresh spinach, cream cheese, herbs and Pernod, 
            then baked and finished with Parmesan cheese.</p>
        </center>
        </Col>
      </Row>

      <div class="modal-footer">
      <Row>
        <button id="closebutton" type="button" class="btn btn-secondary script-text" data-dismiss="modal">Close</button>
      </Row>
      </div>

    </div>
  </div>
</div>
</div>
</Grid>
    );
  }
}

export default Dessert;


